import {call_fetch, admin_path} from './../helpers/fetch_helper';

export const createLender = request  => {
   return call_fetch(admin_path + '/lender', request);
}

export const viewLender = request  => {
   return call_fetch(admin_path + '/lender/view', request);
}

export const listLender = request  => {
   return call_fetch(admin_path + '/lender/list', request);
}

export const updateLender = request  => {
   return call_fetch(admin_path + '/lender/update', request);
}