import React from 'react';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import { Modal , Button} from 'react-bootstrap';
import {listBorrower} from '../../../actions/borrower_actions';
import {person_name,reduce_character} from '../../../helpers/common_helper';
import FlowContainer from '../../common/container/core/FlowContainer';
import AccountContainer from '../../account/container/account_container';
import StatusButton from '../../common/component/core/StatusButton';
import {TooltipLabel} from '../../common/partial';
import {dd_value} from '../../../actions/common_actions';
import $ from 'jquery';
class listBorrowerContainer extends FlowContainer {
  state = {
     borrowerList: null,
     toView: false,
     borrower_id: null,
     borrower_type : null ,
     modalBox: false,
     accountList: null,
  }

  handleClick = borrower_type => event => {
        const cust_id = event.target.id;
        if(borrower_type === "Institution"){
          window.open("/borrower/ins/edit/"+cust_id,"_blank");
        }else if(borrower_type === "Individual"){
          window.open("/borrower/indiv/edit/"+cust_id,"_blank");
        }
    }
  componentWillMount(){
    const country_code = this.country_code;
    listBorrower(this.req({country_code}))
          .then((response) => {
        if(!response){return };
            
          const borrowerList = response.data.map((borrower,i)=><tr key={i}>
                <td > {borrower.cust_id}</td>
                <td > {borrower.data_prvdr_cust_id}</td>
                <td > <TooltipLabel full_text={reduce_character(borrower.biz_name)} overlay={person_name(borrower)} /></td>
                <td > {borrower.mobile_num}</td>
                <td > {borrower.biz_type}</td>
                <td > {borrower.data_prvdr_name} / {borrower.lender_name}</td>
                <td > {borrower.master_cust_id}</td>
                <td > {dd_value(borrower.status)}</td>
                <td > <StatusButton id={borrower.id} entity="borrowers" status={borrower.status}/></td>
                <td > <input id={borrower.cust_id} type='button' onClick={this.handleClick(borrower.biz_type)} value='Edit'  className='btn btn-primary' /> </td>
                <td > <input id={borrower.cust_id} type='button' className=''  onClick={(e) => this.handleViewAccountsClick(e, borrower.data_prvdr_cust_id,person_name(borrower))} value='View Accounts'  class='btn btn-primary' /></td>
              </tr>
             );
              this.setState({borrowerList: borrowerList});
              $('#list_borrower').removeAttr('width').dataTable({

                   "columns": [
                       {"width" : "5%"}, 
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width": "5%"},
                       {"width" : "5%"}, 
                       {"width": "20%"},
                       {"width": "20%"},
                       {"width": "20%"},
                     ],
                    dom:'Bfrtip',
                    buttons: [
                    'csv','excel','print'
                    ]
                });

        });
  }


  componentDidUpdate(){
    // $('#list_borrower').dataTable({ 
    //   "bAutoWidth": true,
    //   "bDestroy": true, 
    // });

  }


  handleViewAccountsClick = (event,data_prvdr_cust_id,person_name) => {
    this.setState({cust_id: event.target.id});
    this.data_prvdr_cust_id =  data_prvdr_cust_id;
    this.person_name = person_name;
    this.setState({modalBox: true});
  }
  handleModalClose = (event) => {
      this.setState({ modalBox: false });
  }



  render(){
   if (this.state.borrowerList != null) {   

    return( 
      <>
      <div id='datatable'>

       <h4>{this.props.title || "Customer List"}</h4> 
             <table id ='list_borrower' className="display strip row-border order-column list">
                      <thead>
                      <tr>
                      <th>FLOW Cust ID</th>
                      <th>Data Provider Cust ID</th>
                      <th>Business Name</th>
                      <th>Mobile Number</th>
                      <th>Customer Type</th>
                      <th>Data Provider / Lender</th>
                      <th>Master Cust ID</th>
                      <th>Status</th>
                      <th>Action</th>
                      <th>Action</th>
                      <th>Account</th>
                      </tr>
                      </thead>
                      <tbody> 
                      {this.state.borrowerList}
                      </tbody>
             </table> 
        </div>
         <Modal dialogClassName="modal-dialog-brwr" style={{width:1000}} show={this.state.modalBox} onHide={this.handleModalClose} >
              <Modal.Header closeButton show={this.state.modalBox} onHide={this.handleModalClose}>
                  <Modal.Title>Customer Accounts : {this.state.cust_id}</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                  <AccountContainer dp_cust_id={this.data_prvdr_cust_id} cust_name={this.person_name} entity="customer" entity_id={this.state.cust_id}/>
              </Modal.Body>
              <Modal.Footer>
                <Button variant="secondary" onClick={this.handleModalClose}>Close</Button>
              </Modal.Footer>
          </Modal>
         </> 
      );
    }
      return (
        <div><p>No borrowers Available ! </p></div>
      );
  }
}
export default listBorrowerContainer;
