import React from 'react';
import '../../../styles/flow.css';
import FlowContainer from '../../common/container/core/FlowContainer';
import {Modal,Button } from 'react-bootstrap';
import SearchLoanCriteria from '../../loan_appl/component/SearchLoanCriteria';
import {searchLoan} from '../../../actions/loan_actions';
import {dd_value} from '../../../actions/common_actions';
import {format_date_tbl, lbl_date,rt_algn} from '../../../helpers/common_helper';
import {lbl_amt} from './../../../helpers/product_helper';
import ViewTxnContainer from './view_transaction_container';
import {CustID, Product} from '../../common/partial';
import $ from 'jquery';

class LoanSearchContainer extends FlowContainer {
	constructor(props){
	    super(props);
	    this.state = {
	        data_prvdr_name_list: [],
	        lender_name_list: [],
	        product_name_list: [],
	        searchLoans: {},
	        datatable_div_style: "hide_div",
	        search_loan:"show_div",
	        modify_search:"hide_div",
	        toView: false  
	    };
	}

    handleSearch = (event) => {
    	var prev_table = $('#list').DataTable();
		prev_table.destroy();
    	this.setState({datatable_div_style: "hide_div"});
    	const loan_search_criteria = this.state.searchLoans;
    	//const currency_code = this.currency_code;
 		if(Object.keys(loan_search_criteria).length > 0){
		  searchLoan(this.req({loan_search_criteria}))
		        .then((response) => {
	          if(!response){return };
		           		var loanResults = [];
		           		if(response.data.results.length > 0){
		           			this.loan =  response.data.results;
							this.setState({search:true})
				           	loanResults = response.data.results.map((loan,i)=> <tr>
                          <td> {loan.loan_doc_id}</td> 
		                  <td><CustID data_prvdr_cust_id={loan.data_prvdr_cust_id} biz_name={loan.biz_name} cust_name={loan.cust_name}/></td>
		                  <td><Product loan={loan} currency_code={this.currency_code}/></td>
		                  <td> {format_date_tbl(loan.disbursal_date)}</td>
		                  <td> {format_date_tbl(loan.due_date)}</td>
		                  <td data-sort={loan.due_amount}> {rt_algn(lbl_amt(loan.due_amount, this.currency_code))}</td>
		                  <td> {loan.paid_date ? lbl_date(loan.paid_date) : "Not Paid"}</td>
		                  <td data-sort={loan.paid_amount}> {rt_algn(lbl_amt(loan.paid_amount, this.currency_code))}</td>              
		                  <td> {dd_value(loan.status)}</td>
		                  <td> <input id={loan.loan_doc_id} type='submit' value='View' onClick={this.viewLoan} className='btn btn-primary' /></td>
		                  <td> <input id={loan.loan_doc_id} type='submit' value='View Txns' onClick={this.viewTxn} className='btn btn-primary' /></td>
		                    </tr>
		                    );
			           		
		           			this.setState({datatable_div_style: "show_div",loanResults: loanResults,search_loan:"hide_div",modify_search:"show_div"});
		           			$('#list').removeAttr('width').dataTable({
		           	  		  "order" : [],
						      "columns": [
		           			   {"width": "20%"},
				               {"width": "10%"},
				               {"width": "5%"},
				               {"width": "10%"},
				               {"width": "10%"},
				               {"width": "15%"},
				               {"width": "10%"},
				               {"width": "10%"},
				               {"width": "5%"},
				               {"width": "5%"},
				               {"width": "5%"}
		           			 ],
						      dom:'Bfrtip',
						      buttons: [
                                 'csv','excel','print'
						      ]
						    });	

			           	}else{
			          		alert("No results found.");   		
			           	}
		          }
		        );
		}else{
			alert("Choose atleast one filter to search Float Advance.")
		}					        
	}

	viewLoan = (event) => {
		const loan_doc_id = event.target.id;
		window.open("/fa/view/"+loan_doc_id,"_blank");
	}

	viewTxn = (event) => {
		this.setState({loan_doc_id : event.target.id});
		this.setState({modalBox: true});
	}

	handleModalClose = (event) => {
      this.setState({ modalBox: false });
   }
	handleModifySearch = (event) => {
	this.setState({
		search_loan:"show_div",
		modify_search:"hide_div"
	})
	}

	render(){
		return(
			<div className="container">
				<div className="row">
					<h3 className="search_loan">SEARCH - Float Advance </h3>
				</div>
				<br/>
                <div className={this.state.modify_search}>

				<button type="button" className="btn btn-md modify" id="float_style"onClick={this.handleModifySearch} >Modify Search</button>

				</div>
				 <div className={this.state.search_loan}>
					<SearchLoanCriteria id="searchLoans" mode="loan" 
										onSearch={this.handleSearch} 
										onComponentChange={this.handleComponentChange} 
										onComponentReset={this.handleComponentReset} 
										onComponentElementChange={this.handleComponentElemetChange}/>
		           </div>
				<div id="">		
					<div className={this.state.datatable_div_style}>
						<div id=''>
						
            			<br/>
		                <table id ='list'  className='display strip row-border order-column list'>
		                  <thead>
		                  <tr>
		                  <th className="loanid">FA ID</th>
		                  <th>Data Provider Cust ID</th>
		                  <th>Product Name</th>
		                  <th>Disbursal Date</th>
		                  <th>Due Date</th>
		                  <th>FA With Fee</th>
		                  <th>Payment Date</th>
		                  <th>Payment Amount</th>
		                  <th>Status</th>
		                  <th>Action</th>
		                  <th>Action</th>
		                  </tr></thead>
		                  <tbody> 
		                 {this.state.loanResults}
		                  </tbody>
		                </table>   
		              </div>    
					</div>
				</div>

			<Modal show={this.state.modalBox} onHide={this.handleModalClose} size="xl">
             	<Modal.Header closeButton show={this.state.modalBox} onHide={this.handleModalClose} >
                 	 <Modal.Title><h4>Transactions of Float Advance ID: {this.state.loan_doc_id}</h4></Modal.Title>
              	</Modal.Header>
              	<Modal.Body>
             		<ViewTxnContainer loan_doc_id={this.state.loan_doc_id} />
             	</Modal.Body>
             	<Modal.Footer>
                	<Button variant="secondary" onClick={this.handleModalClose}>Close</Button>
              	</Modal.Footer>
          	</Modal>
			</div>
			);
	}
}
export default LoanSearchContainer;
