import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';

import {check_priv, get} from '../../../helpers/storage_helper';
import {format_date} from '../../../helpers/common_helper';

import {Modal, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {additionalApproval} from '../../../actions/borrower_actions';


class RMApprovalModal extends FlowComponent {
	state = {
		showModal: false,
		days_div_style: "hide_div",
		disabled: ''
	}
	handleOpen = (event) => {
		const can_approve = check_priv('customer', 'addl_approval') && this.is_user_rel_mgr()
		if(can_approve){
			this.setState({showModal: true})
		}else{
			alert("You do not have privilege to approve. Relationship Manager " + this.props.customer.flow_rel_mgr_name + " can approve")
		}
	}
	handleClose = (event) => {
		this.setState({showModal: false});
	    
	}

	is_user_rel_mgr(){
		//return true;
		return this.props.customer.flow_rel_mgr_id == get('user_id')
	}
	componentDidMount() {
		this.addl_approval = {
											approve: 'false',
											reject: 'false'}

	}

	handleApproveCheckbox = (event) => {
		const id = event.target.id;
		var days_div_style =  "hide_div";
		this.setState({'approve' : false, "reject" : false, days_div_style})
		
		if(event.target.checked){
			if(id == "approve"){
				days_div_style = "show_div"
			}
			this.setState({[id] : event.target.checked, days_div_style})
			event.target.value = 'true'	
		}else{
			event.target.value = 'false'
		}
		this.handleSelfChange(event);
	}
	
	handleSubmitApproval = (event) => {
		this.addl_approval.flow_rel_mgr_id =  this.props.customer.flow_rel_mgr_id;
		this.addl_approval.cust_id = this.props.customer.cust_id

		additionalApproval(this.req({addl_approval: this.addl_approval}))
        .then((response) => {
          if(!response){return };
            if(response.status === "success"){
                alert(response.message);  	
              this.setState({ showModal: false});
            }else{
              alert(response.message);  
            }
          }
        );

	}


render(){
	
		return(
			<>
				{check_priv('customer','addl_approval') &&
					<Link to="#" onClick={this.handleOpen} 
					className="agreement">Action on FA Products that Require Additional Approval</Link>			
				}
				
			
				<Modal show={this.state.showModal} onHide={this.handleClose} backdrop="static" size="lg">
					<Modal.Header closeButton show={this.state.showModal} onHide={this.handleClose}>
					  <Modal.Title><h3>Action on FA Products that Require Additional Approval</h3></Modal.Title>
					</Modal.Header>

				    <Modal.Body>
						<>
				   		{/*<div className="row">
				   			<div className="col-md-12">
				   			<font className="text-danger"> Note : This approval will make the customer ({this.props.customer.cust_id} {this.props.customer.biz_name}) able to purchase float products which he/she is marginally eligible.</font>
				   			</div>
				   		</div>*/}
				   		{this.props.customer.addl_appr_until && 
					   		<div className="row">
					   			<div className="col-md-12">
					   			<font className="text-danger">Already Approved Until : {format_date(this.props.customer.addl_appr_until)}</font>
					   			</div>
					   		</div>
				   		}

				      		I am the Relationship Manager for this customer,
				      	<div className="row">
				   			<div className="col-md-12">
					   		<label><input id="approve" checked={this.state.approve}  type="checkbox" onChange={this.handleApproveCheckbox}></input>I herewith provide additional approval required to process FA products that lacks credit score.</label>
					    	</div>	
				      	</div>
				      
				       	<div className="row">
				   			<div className="col-md-12">
					   		<label><input id="reject" checked={this.state.reject} type="checkbox" onChange={this.handleApproveCheckbox}></input>I reject allowing this customer to process FA products that lacks credit score</label>
					    	</div>	
				      	</div>
				      	<br/>	
				      	<div className={`${this.state.days_div_style}`} >
				      	<div className="row" >
				      		<div className="col-md-2">
				      			<span>Approval Valid for </span>
				      		</div>
				      		<div className="col-md-2">
				                <select id="days"  onChange={this.handleSelfChange} className="form-control" required >
				                  <option value="">Choose</option>
				                  <option value="2">2 days</option>
				                  <option value="5">5 days</option>
				                  <option value="10">10 days</option>
				                  <option value="15">15 days</option>
				                  <option value="30">30 days</option>
				                  <option value="60">60 days</option>
				                  <option value="99">Forever</option>
				                </select> 
			                </div>
			                from today
				      	</div>
				      	</div>	
				     </>
				    </Modal.Body>

				    <Modal.Footer>
				     		<Button variant="secondary" onClick={this.handleClose}>Cancel</Button>
				            <input type="button" className="btn btn-primary" onClick={this.handleSubmitApproval} value="Submit"/>  
				    </Modal.Footer>
			  </Modal>
  </>
  );
}

}
export default RMApprovalModal;