import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import '../../../styles/flow.css';
import {Alert,Row, Col} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {format_date} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';

class Agreement extends FlowComponent {
	render(){

		return(
				<div>

					{this.props.customer.current_aggr_doc_id && 	
						<Alert variant={this.props.variant}>	
							<Row>
						      <Col md={2}>
		                       <Alert.Heading><b>Agreement</b></Alert.Heading>
						      </Col>
							</Row>
							<Row>
								<Col>Agreement ID</Col>
								{this.props.customer.current_aggr_doc_id &&
								<Col> <Link to={this.props.customer.aggr_file_rel_path ? this.props.customer.aggr_file_rel_path : "#"} target="_blank">{this.props.customer.current_aggr_doc_id ? this.props.customer.current_aggr_doc_id : "No Agreement"}</Link></Col>	
						        }
								<Col>Valid Upto</Col>
								<Col as='b'>{this.props.customer.aggr_valid_upto ? format_date(this.props.customer.aggr_valid_upto) : "NA"}</Col>
							</Row>
							<Row>
		                         <Col>Status</Col>
								<Col as='b'>{this.props.cust_agreement_status ? dd_value(this.props.cust_agreement_status) : "NA"}</Col>
								 <Col></Col>
								<Col></Col>
							</Row>
						</Alert>
					}
				</div>
			);
	}
}

export default Agreement;