import {  Redirect } from 'react-router-dom';
import React from 'react';
import {createBorrower, viewBorrower} from '../../../actions/borrower_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import Borrower from '../component/Borrower';
import AddressInfo from '../../common/component/AddressInfo';
import Person from '../../common/component/Person';
import '../../../styles/flow.css';

class IndividualBorrowerContainer extends FlowContainer {
  
  
  state = {
    logout : super.touch_async(),
    toList: false,   
    cust_id: null,
    biz_address: {touch: null},
    owner_person : {touch: null},
    owner_address: {touch: null},
    contact_person : {touch: null},
    borrower: {},
    address_div_style: "show_div",
    person_div_style: "show_div",
    send_country_code: true,
    same_as_biz_address: false,
    same_as_owner_person:false,

  }

  componentWillMount() {
    
    if(this.props.mode === 'view'){
      this.cust_id = this.props.match.params.cust_id;
      viewBorrower(this.req({cust_id: this.cust_id, screen: "edit"}))
        .then((response) => {
          if(!response){return };
            this.borrower = response.data;
            this.setState({borrower: this.borrower,
                            biz_address: this.borrower.biz_address,
                            owner_person: this.borrower.owner_person,
                            owner_address: this.borrower.owner_person.owner_address,
                            contact_person: this.borrower.contact_person
                           });
          }
        );
    }else if(this.props.mode  === 'create'){
     
    }
  } 

  handleSubmit = (event)=>{
    event.preventDefault();
    var borrower = this.state.borrower;
    borrower.country_code = this.country_code;
    borrower.biz_type = "Individual";
    borrower.biz_address = this.state.biz_address;
    borrower.owner_person = this.state.owner_person;
    borrower.owner_person.owner_address = this.state.owner_address; 
    borrower.owner_person.country_code = this.country_code;
    borrower.contact_person = this.state.contact_person;
    borrower.same_as_biz_address = this.state.same_as_biz_address;
    borrower.same_as_owner_person = this.state.same_as_owner_person;
    if(this.state.send_country_code){
       borrower.contact_person.country_code = this.country_code;
    }
    createBorrower(this.req({borrower}))
        .then((response) => {
          if(!response){return };
            if(response.status === "success"){
              this.setState({cust_id: response.data});
              this.setState({toList: true});
              alert("New Individual Customer created successfully");
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          }
        );
  }
  handleAddressCheckbox = (event) => {
    if(event.target.checked)
    {
        this.setState({address_div_style: "hide_div" , 
                       same_as_biz_address:true});
    }
    else
    {
        this.setState({address_div_style: "show_div",
                      same_as_biz_address:false});   
    }
  }

  handlePersonCheckbox = (event) => {
    if(event.target.checked)
    {
        this.setState({person_div_style: "hide_div",
                        send_country_code: false,
                        same_as_owner_person:true});
    }
    else
    {
        this.setState({person_div_style: "show_div",
                      send_country_code: true,
                     same_as_owner_person:false});

    }
  }

 
  render(){
    
    this.state.logout.then((value) => this.logout = value)
    if(this.logout){
       return <></>
    }
    
    if (this.state.toList === true) {
      return <Redirect to={`/borrower/indiv/edit/${this.state.cust_id}`}/> 
    }
      return (
 
      <div className="container">
         <h4>{this.props.title + (this.state.borrower.name? " : " + this.state.borrower.name : "") +" "+(this.cust_id? this.cust_id : "")}</h4> 
        <form id="borrower_form" onSubmit={this.handleSubmit}>
            <div id="">
                <Borrower id="borrower" data={this.state.borrower} screen={this.props.screen} mode={this.props.mode} onComponentChange={this.handleComponentChange}/>
                <hr/>
                <AddressInfo id="biz_address" title="Business Address" gps={this.state.gps} data={this.state.biz_address} mode={this.props.mode}  onComponentChange={this.handleComponentChange}
                onComponentReset={this.handleComponentReset}/>

                <Person id="owner_person" title="Business Owner Details" val="Business Owner Details" data={this.state.owner_person} mode={this.props.mode} onComponentChange={this.handleComponentChange}/>
                <br/>

              {(this.props.mode === "view") ?
                <div>
                  <AddressInfo id="owner_address" title="Business Owner Address" data={this.state.owner_address}  mode={this.props.mode} onComponentChange={this.handleComponentChange} onComponentReset={this.handleComponentReset}/>
                  <Person id="contact_person" title="Contact Person" val="Borrower Contact Person" data={this.state.contact_person} mode={this.props.mode} onComponentChange={this.handleComponentChange}/>
                <br/>
                <br/>
                </div>
               :
                <div>
                <label className="checkbox-inline">
                    <h4>Business Owner Address&nbsp;&nbsp;<input type="checkbox" onClick={this.handleAddressCheckbox} />&nbsp;&nbsp;{"Same as Business Address"}</h4>
                    </label>
                    <div className={`${this.state.address_div_style}`} >
                      <AddressInfo id="owner_address" data={this.state.owner_address} mode={this.props.mode} onComponentChange={this.handleComponentChange} onComponentReset={this.handleComponentReset}/>        
                      <hr/>  
                    </div><br />
                     <label className="checkbox-inline">
                    <h4>Contact Person&nbsp;&nbsp;<input type="checkbox" onClick={this.handlePersonCheckbox} />&nbsp;&nbsp;{"Same as Business Owner"}</h4>
                    </label>
                    <div className={`${this.state.person_div_style}`} >
                      <Person id="contact_person" title="" val="Borrower Contact Person" data={this.state.contact_person} mode={this.props.mode} onComponentChange={this.handleComponentChange}/>
                      <br />
                    </div>
                 </div>
              }
            <input type="button" className={`btn btn-primary ${this.props.mode === "create" ? "show_button": "hide_button"}`} onClick={this.handleSubmit} value="Create New Customer" />
          </div>

        </form>  
        <br/><br/>
      </div>

      );
  }
}

export default IndividualBorrowerContainer;