import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {get,get_item} from './../../helpers/storage_helper';
import './../../styles/flow.css';
import Flag from "react-flags";
import {MarketRoutes, LenderRoutes, DataPrvdrRoutes, AccPrvdrRoutes, CSFRoutes,
            ProductRoutes, MasterDataRoutes} 
            from "./../../routes/admin_routes";
import {CustomerRoutes, FLOWRelMgrRoutes, DPRelMgrRoutes, LoanApplRoutes, LoanRoutes} 
             from "./../../routes/user_routes";
import SelectMarket from './SelectMarket';
import Logo from './../common/partials/logo';

export class AdminMenu extends  Component {
  state = {
    toSelectMarket:false
  }

  componentWillMount(){
    this.selectMarket = this.selectMarket.bind(this);
    const market = get('market');
    this.country_code = market.country_code; 
    this.data_prvdr_code = get_item('data_prvdr_code')
    this.app_version = get('app_version');
    this.user_name = get('user_name');
    this.prv = get('privileges');
    this.roles = get('role_codes');
    this.data_prvdr_logo = get_item('data_prvdr_logo');

    Object.keys(this.prv).map((key) => {
      const priv_tokens = this.prv[key].split(',')
      this[key] = {};      
      
      priv_tokens.map((priv) => {
        this[key][priv] = true;
      });

    }); 
  }

    selectDataPrvdr = (event) => {
      if(this.roles === "super_admin"){
        this.toChange = "data_prvdr";  
      }else{
        this.toChange = "data_prvdr_only";  
      }    
      
      this.setState({toSelectMarket:true});
    }
  
    selectMarket = (event) => {
  
      if(get('role_codes') ==="super_admin"){
        this.toChange = "market";
        this.setState({toSelectMarket:true});
      }
    }
  render(){
      if(this.state.toSelectMarket){
      //alert(this.toChange)
       return (<SelectMarket toChange={this.toChange} action="change"/>);
      }
  return (
  <>
  <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
   {/* <a className="navbar-brand" href="#">
      <img src="" width="30" height="30" className="d-inline-block align-top" alt=""></img>
      FLOW
    </a>*/}
    <img src="/logo.png" />
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>

    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mr-auto">

        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Search
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
            {this.customer && this.customer.search_list_view && 
             <>
            <Link className="dropdown-item" to={'/borrower/search'}>Search Customer</Link>
            </>}
            {this.loan && this.loan.search_list_view &&
             <Link className="dropdown-item" to={'/fa/search'}>Search Float Advance</Link>}
             <div className="dropdown-divider"/>
            {this.application && this.application.search_list_view &&
             <Link className="dropdown-item" to={'/fa_appl/search'}>Search FA Applications</Link>}
          </div>
        </li>

        {(this.application ) &&
        <li className="nav-item dropdown">
          {this.application.apply &&
             <Link className="nav-link apply_fa" to={'/fa_appl'}>Apply FA</Link>}
        </li>
      }

      {this.report &&
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Reports
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          {this.report.flow_kpi_dashboard && 
              <>
             <Link className="dropdown-item" to={'/report/monthly_kpi_report'}>Monthly KPI Report</Link>   
             <Link className="dropdown-item" to={'/report/current_kpi_report'}>Current KPI Report</Link>   
              </>
          }
          {this.report.portfolio_quality && 
             <Link className="dropdown-item" to={'/report/portfolio_quality'}>Portfolio Quality</Link>  }
          {this.report.growth_chart && 
             <Link className="dropdown-item" to={'/report/growth_chart'}>Growth Chart</Link>    
          
         }
          </div>
        </li>
      }

       

        {this.customer && this.customer.create &&
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Customer
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
             
             <>
            <Link className="dropdown-item" to={'/borrower/indiv/create'}>Add Individual Customer</Link>
            <Link className="dropdown-item" to={'/borrower/ins/create'}>Add Institutional Customer</Link>
            {/* <Link className="dropdown-item" to={'/upload_cust_txns'}>Upload Customer Transaction</Link>  */}
            </>
          </div>
        </li>
      }

       {this.product &&
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Product
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
           {this.product.create &&
           <Link className="dropdown-item" to={'/fa_product/create'}>Add Product</Link>}         
           {this.product.search_list_view &&
           <Link className="dropdown-item" to={'/fa_product/list'}>Search Products</Link> }
          {this.template &&
            <>
           <div className="dropdown-divider"/>
           {this.template.create &&
           <Link className="dropdown-item" to={'/fa_product/template/create'}>Add Product Template</Link>}
           {this.template.list_view &&
           <Link className="dropdown-item" to={'/fa_product/template/list'}>Search Product Templates</Link>}
           </>
         }
          </div>
        
        </li>
      }

         {this.score &&
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Score
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          {this.score.list &&
              <Link className="dropdown-item" to={'/score_models'}>Score Models</Link> 
          }
          {this.score.factor_list &&
              <Link className="dropdown-item" to={'/score_factors'}>Score Factors</Link> 
          }
          {this.score.cust_txn &&
              <Link className="dropdown-item" to={'/upload_cust_txns'}>Upload Customer Transaction</Link>  
          }
          </div>
        </li>
      }

        {this.master_agreement &&
       <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Agreement
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          {this.master_agreement.create &&
              <Link className="dropdown-item" to={'/agreement/create'}>Create Master Agreement</Link>
          }
          {this.master_agreement.list_view &&
              <Link className="dropdown-item" to={'/agreement/list'}>List Master Agreement</Link>
          }
          </div>
      </li>
      }

        {(this.data_prvdr || this.data_prvdr_rel_mgr )&&
         <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Data Provider
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          {this.data_prvdr &&
            <>
           {this.data_prvdr.create && 
            <Link className="dropdown-item" to={'/data_provider/create'}>Create Data Provider</Link>}
            {this.data_prvdr.list_view && 
            <Link className="dropdown-item" to={'/data_provider/list'}>List Data Providers</Link>}
            <div className="dropdown-divider"/>
            </>

           }
           {this.data_prvdr_rel_mgr && 
            <>
            
            {this.data_prvdr_rel_mgr.create && 
            <Link className="dropdown-item" to={'/relationship_manager/dprlspmanager/create'}>Add  Rel. Manager</Link>}
            {this.data_prvdr_rel_mgr.list_view && 
            <Link className="dropdown-item" to={'/relationship_manager/dprlspmanager/list'}>List Rel. Managers</Link>}
            </>
            }
          </div>
        </li>
        }

          {this.lender &&
         <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Lender
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
           {this.lender.create && 
            <Link className="dropdown-item" to={'/lender/create'}>Create Lender</Link>}
             {this.lender.list_view && 
            <Link className="dropdown-item" to={'/lender/list'}>List Lenders</Link> }
          </div>
        </li>
      }
       
      {(this.market || this.acc_prvdr || this.flow_rel_mgr) &&
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            FLOW (Market)
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          {this.market &&
            <>
          {this.market.create && 
           <Link className="dropdown-item" to={'/market/create'}>Create Market</Link>}

           {this.market.list_view && 
           <Link className="dropdown-item" to={'/market/list'}>List Markets</Link>}
            <div className="dropdown-divider"/>
            </>
          }
          {this.acc_prvdr && this.acc_prvdr.create && 
             <Link className="dropdown-item" to={'/account_provider/create'}>Create Account Provider</Link>
              } 
              {this.acc_prvdr && this.acc_prvdr.list_view && 
             <Link className="dropdown-item" to={'/account_provider/list'}>List Account Providers</Link>
            }
              <div className="dropdown-divider"/>

           {this.flow_rel_mgr && this.flow_rel_mgr.create && 
           <Link className="dropdown-item" to={'/relationship_manager/flowrlspmanager/create'}>Add FLOW Rel. Manager</Link>}
           {this.flow_rel_mgr && this.flow_rel_mgr.list_view && 
           <Link className="dropdown-item" to={'/relationship_manager/flowrlspmanager/list'}>List FLOW Rel. Managers</Link>}
          </div>
          


        </li>
      }

      
      {this.master_data &&
         <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Master Data
          </a>
          <div className="dropdown-menu" aria-labelledby="navbarDropdown">
           {this.master_data.create &&
            <Link className="dropdown-item" to={'/master_data/add'}>Add Master Data</Link>}
            <div className="dropdown-divider"/>
            {this.data_key.create &&
             <Link className="dropdown-item" to={'/master_data_key/add'}>Add Master Data Key</Link>
            }
              {this.data_key.list_view &&
             <Link className="dropdown-item" to={'/master_data_key/list'}>List Master Data Keys</Link>}
        


          </div>
        </li>
      }

      </ul>



      
    </div>
   
    <div className="pull-right">
      <ul  className="pull-right">
        <li><span className="text_color">App Version: {this.app_version}</span>&nbsp;&nbsp;</li>
        <li><span className="text_color">{this.user_name} ({this.roles})</span></li>
        <li><a href="/logout"><i className="icon-off"></i>Logout</a></li>
      </ul> 
  </div>
  <div className={get('role_codes') == 'super_admin' && "flag_cursor"} onClick={this.selectMarket}>
      <Flag id="market" name={this.country_code} format="png" pngSize={48} shiny={false} alt={this.country_code} basePath="/img/flags"/>
  </div>
  <div className="flag_cursor" onClick={this.selectDataPrvdr}>
      <Logo file_name={"t_"+this.data_prvdr_logo} entity="data_prvdr" 
                entity_code={this.data_prvdr_code} 
                country_code={this.country_code} />&nbsp;&nbsp;
  </div>  

</nav>
</>
      );
  }
}


export default AdminMenu;