import { Redirect } from 'react-router-dom';
import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import '../../../styles/flow.css';
import {getCustPerformance} from '../../../actions/report_actions';
import {lbl_amt,new_object} from './../../../helpers/common_helper';
import {Chart} from 'primereact/chart';
export class GrowthChart extends FlowComponent{
    state = {
     cust_perform_search:"hide_div"
  }
  componentWillMount()
  {
    this.cust_id =this.props.match.params.cust_id;
    if(this.cust_id !="")
    {
    this.handleSearchClick();
  }
  }

  handleCustIDChange = (event)=>
  {
    this.cust_id = event.target.value;
  }

  handleEnter = (event) => {
    if (event.keyCode === 13) {
    this.handleSearchClick();
    }
  }

  handleSearchClick = (event)=>
  {
  if(this.cust_id != null)
  {
     const cust_id = this.cust_id;
        getCustPerformance(this.req({cust_id}))
          .then((response) => {
            if(!response) {return;}
               if(response.status === "success"){
                const cust_perform = new_object(response.data);
                this.date = cust_perform.date;
                this.amount = cust_perform.amount;
                this.loan_doc_id = cust_perform.loan_doc_id;
                this.setState({
                cust_perform_search:"show_div",searchResult:true
                })
             }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
             }); 
        }
  }
render(){

    if (this.state.searchResult === true && this.props.match.params.cust_id =="") { 
      return <Redirect to={`/report/growth_chart_for/${this.cust_id}`} />

    }
     const data = {
            labels: this.date,
            datasets: [
                {
                    label: "Float Amount "+ this.currency_code,
                    backgroundColor: '#42A5F5',
                    data: this.amount,
                    loan_doc_ids : this.loan_doc_id
                }
            ]    
        };
         const multiAxisOptions = {
            tooltips : {
              callbacks : {
                afterBody: function(t,data){
                  return "FA ID : "+data.datasets[0].loan_doc_ids[t[0].index]
                }
              }
            },
            scales: {
                yAxes: [{
                    type: 'linear',
                    display: true,
                    position: 'left',
                    id: 'y-axis-1',
                    ticks: {
                        min: 0,
                        callback: 
                          function(value, index, valuesArray){
                            
                            return lbl_amt(value, "UGX")
                          }     
                  },
                  scaleLabel: {
                    display: true,
                    labelString: "Float Amount"
                  }
                }],
                xAxes: [{
                    scaleLabel: {
                    display: true,
                    labelString: "Disbursal Date"
                  }

                }]
            }
        }
      return (
               <div id="">
               <div className="container">
                <div className="row top">
                  <div className="col-md-2">
                    <span><h5>Flow Customer ID </h5></span> 
                  </div>
                  <div className="col-md-4">
                    <div className="row">
                      <div className="col-md-8">
                          <input id="customer_id" onChange={this.handleCustIDChange} onKeyUp={this.handleEnter} placeholder="Enter..." className="form-control" required />
                      </div>
                      <div className="col-md-4">
                          <input type="button" value="Search" onClick={this.handleSearchClick}/>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
               <div className={this.state.cust_perform_search}>
               <div classNmae="container">
                   <h4 className="chart">Growth Chart</h4>
                <div className="content-section implementation">
                    <Chart type="bar" data={data} options={multiAxisOptions} style={{width:'900px',marginLeft:'100px'}}/>
              </div>
            </div>
              </div> 
       </div>
);
}
}
export default GrowthChart;