import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import '../../../styles/flow.css';
import {Alert,Row, Col } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
//import CreateAgreement from './CreateAgreement';
import Agreement from '../../borrower/component/Agreement';
import RMApprovalModal from '../../borrower/component/RMApprovalModal';
import {check_priv} from '../../../helpers/storage_helper';

class CustPanel extends FlowComponent {
	state = {
		variant: "primary"
	};
	
	componentDidMount(){
		
		if(this.props.parent === 'apply' || this.props.parent === 'selection' ){
			this.setState({variant: 'dark'})
		}else if(this.props.parent === 'loan_appl'){
			this.setState({variant: 'success'})
		}else if(this.props.parent === 'loan'){
			this.setState({variant: 'primary'})
		}
	
	}

	/*handleAgreementLinkClick = () => {
		if(this.props.customer.account_id == null){
			this.setstate({link_clickable : false});
			alert("No Account Configured for Customer");
			return;
		}	
	}*/



Customer = () => (
				<>	
					<Alert.Heading>
					<b>Customer : {this.props.customer.cust_id}</b>
					{this.props.parent === 'selection' &&
					<RMApprovalModal id="addl_approval" 
					customer={this.props.customer}/>
				}
					</Alert.Heading>
					<Row>
						<Col>Business Name </Col>
						<Col as='b'>{this.props.customer.biz_name}</Col>
						<Col>Customer Name </Col>
						<Col as='b'>{this.props.customer.cust_name}</Col>
						
					</Row>
					<Row>
						<Col>FLOW Cust ID</Col>
						<Col as='b'>{this.props.customer.cust_id}</Col>
						<Col>{this.props.customer.data_prvdr_name} Cust ID</Col>
						<Col as='b'>{this.props.customer.data_prvdr_cust_id}</Col>
					</Row>
					<Row>
						<Col>Mobile Number</Col>
						<Col as='b'>{this.props.customer.cust_mobile_num}</Col>
						<Col>Customer Type</Col>
						<Col as='b'>{this.props.customer.biz_type}</Col>
					</Row>	
					<Row> 
						<Col md={3}>Address</Col>
						<Col md={9} as='b'>{this.props.customer.cust_addr_text}</Col>
						
					</Row>		
				</>	
				)


ReltnShip = () => (
				<>

					<Alert.Heading><b>Relationship</b></Alert.Heading>
					<Row>
						<Col md={3}>Relationship Manager</Col>
						<Col  md={9} as='b'><span>{this.props.customer.flow_rel_mgr_name} (m: {this.props.customer.flow_rel_mgr_mobile_num})<br/></span></Col>
					</Row>
					<Row>
						<Col md={3}>Data Provider</Col>
						<Col  md={9} as='b'>{this.props.customer.data_prvdr_name}&nbsp;
						  - {this.props.customer.dp_rel_mgr_name} (m: {this.props.customer.dp_rel_mgr_mobile_num})</Col>
					</Row>
					<Row>
						<Col md={3}>Lender</Col>
						<Col  md={9} as='b'>{this.props.customer.lender_name}</Col>
					</Row>
				</>	

				)

render(){
	
	if(this.props.parent === 'selection' ){
		return(
		<>
			<Alert variant={this.state.variant}>
					{this.Customer()}
			</Alert>	

			<Alert variant={this.state.variant}>
					{this.ReltnShip()}
			</Alert>

			
			<Agreement mode="create" variant={this.state.variant} 
				customer={this.props.customer} cust_agreement_status={this.props.cust_agreement_status} 
				 />
			
			{this.props.parent === "selection" && this.props.loan_products &&
				<>
				<Link to={this.props.customer.cust_id ? `/agreement/list/${this.props.customer.cust_id}` : "#"} 	 
				className={this.props.customer.account_id ? "agreement" : "agreement_disable"}
				onClick={this.handleAgreementLinkClick} target="_blank">List Customer Agreements</Link>
				{check_priv('cust_agreement','create') &&
					<Link to={this.props.customer.cust_id ? `/agreement/create/${this.props.customer.cust_id}` : "#"} 	 
					className={this.props.customer.account_id ? "agreement" : "agreement_disable"}
					onClick={this.handleAgreementLinkClick} target="_blank">Generate New Customer Agreement</Link>
				}
				{check_priv('cust_agreement','sign_upload') &&
					<Link to={`/agreement/upload/${this.props.customer.cust_id}`} 
					className={this.props.customer.account_id ? "agreement" : "agreement_disable"} 
					onClick={this.handleAgreementLinkClick} target="_blank">Sign / Upload Agreement</Link>
				}
				</>
			}
			
				
			
			

			{!this.props.customer.account_id &&
				
				<label className="agreement">( Please configure account for customer to generate / upload agreement. )</label>
				
			}
			
			
		</>
		);
	}else{
		return(
				<>
					<Alert variant={this.state.variant}>
							{this.Customer()}
						<hr/>
							{this.ReltnShip()}
					</Alert>	

					<Agreement customer={this.props.customer} variant={this.state.variant} loan_products={this.props.loan_products} />					
					
				</>
		);
	}
}
}


export default withRouter(CustPanel);