import React from 'react';
import { Link} from 'react-router-dom';
import '../../../styles/flow.css';
import FlowContainer from '../../common/container/core/FlowContainer';
import SearchLoanCriteria from '../../loan_appl/component/SearchLoanCriteria';
import {borrowerSearch} from '../../../actions/borrower_actions';
import {dd_value} from '../../../actions/common_actions';
import { Modal , Button} from 'react-bootstrap';
import {person_name, calc_percent} from '../../../helpers/common_helper';
import AccountContainer from '../../account/container/account_container';
import StatusButton from '../../common/component/core/StatusButton';
import {TooltipLabel,Remarks} from '../../common/partial';
import {check_priv} from '../../../helpers/storage_helper';
import DatePicker from 'react-datetime';
import moment from 'moment';
import $ from 'jquery';

class BorrowerSearchContainer extends FlowContainer{
	constructor(props){
	    super(props);
	this.state = {
		searchBorrower:{},
	    datatable_div_style: "hide_div",
	    search_div_style: "show_div",
	    modalBox: false,
	    modify_search:"hide_div",
        accountList: null

	};
 }
  /*handleClick = borrower_type => event => {
        const cust_id = event.target.id;
        if(borrower_type === "Institution"){
          window.open("/borrower/ins/edit/"+cust_id,"_blank");
        }else if(borrower_type === "Individual"){
          window.open("/borrower/indiv/edit/"+cust_id,"_blank");
        }
    }*/
  handleViewAccountsClick = (event,data_prvdr_cust_id,person_name) => {
    this.setState({cust_id: event.target.id});
    this.data_prvdr_cust_id =  data_prvdr_cust_id;
    this.person_name = person_name;
    this.setState({modalBox: "true"});
  }
  handleModalClose = (event) => {
      this.setState({ modalBox: false });
  }  
  handleModifySearch = (event) => {
	this.setState({
		search_div_style:'show_div',
		modify_search:'hide_div'
	})
  }  
  handleClick = (mode , borrower_type, cust_id) => event => {
  	event.preventDefault();
  	
    if(borrower_type === "Institution"){
      window.open("/borrower/ins/"+mode+ "/" +cust_id,"_blank");
    }else if(borrower_type === "Individual"){
      window.open("/borrower/indiv/"+mode+ "/" +cust_id,"_blank");
    }
  }

  	showBorrowers(){

  	const can_edit = check_priv('customer', 'edit');
  	const r = Math.random();
	const borrowerResults = this.borrower.map((borrower,i)=> <tr key={r+i}>
        <td ><a href="#" className="flow_custid" onClick={this.handleClick('view', borrower.biz_type, borrower.cust_id)} target="_blank">{borrower.cust_id}</a></td>
        <td > {borrower.data_prvdr_cust_id}</td>
        <td > {borrower.category}</td>
        <td > <TooltipLabel full_text={borrower.biz_name} overlay={person_name(borrower)} /></td>
        <td > {borrower.mobile_num}</td>
        
        <td > {borrower.data_prvdr_name}</td>
        <td > {borrower.tot_loans ? borrower.tot_loans : 0}</td>
        <td > {calc_percent(borrower.late_loans, borrower.tot_loans)}</td>
		
        <td > 
		{borrower.is_og_loan_overdue && borrower.ongoing_loan_doc_id ?  
		<Link to={"/fa/view/"+borrower.ongoing_loan_doc_id} target="_blank" className="overdue">{borrower.ongoing_loan_doc_id}</Link>
		:
		borrower.ongoing_loan_doc_id ?
		<Link to={"/fa/view/"+borrower.ongoing_loan_doc_id} target="_blank" className="on_going">{borrower.ongoing_loan_doc_id}</Link>
		:
		borrower.pending_loan_appl_doc_id ?  
		<Link to={"/fa_appl/view/"+borrower.pending_loan_appl_doc_id} target="_blank" className="pending">{borrower.pending_loan_appl_doc_id}</Link>
		:
		<Link to={"/fa_appl_for/"+borrower.cust_id} target="_blank" className="apply">Apply FA</Link>
		}
		</td>    
        <td ><Remarks remarks={borrower.remarks} /></td>  
        <StatusButton id={borrower.id} entity="borrowers" status={borrower.status}/>

        {can_edit &&
        	<td > <input id={borrower.cust_id} type='button' onClick={this.handleClick('edit' , borrower.biz_type, borrower.cust_id)} value='Edit'  className='btn btn-primary' /> </td>
    	}
        <td > <input id={borrower.cust_id} type='button' onClick={(e) => this.handleViewAccountsClick(e, borrower.data_prvdr_cust_id,person_name(borrower))} value='View A/C'  className='btn btn-primary' /></td>

        <td>
        <Link to={"/report/growth_chart_for/"+borrower.cust_id} target="_blank" className="btn btn-primary">Growth</Link>
        </td> 

       </tr>
        );
   		this.setState({can_edit, search_div_style:'hide_div', borrowerResults, datatable_div_style: "show_div",modify_search:"show_div"});
        var col_width = [
                {"width" : "8%"}, 
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"},
				{"width": "8%"}
                 ];

          if(can_edit){
             col_width.push({"width": "8%"});
          }


	        $('#borrower_list').removeAttr('width')
	        	.dataTable({
	          	"order" : [],
				"columns": col_width,

			dom:'Bfrtip',
			buttons: [
			'csv','excel','print'
			]
			});		
  	}
 	 handleSearch = (event) => {
    	var prev_table = $('#borrower_list').DataTable();
		prev_table.destroy();
    	this.setState({datatable_div_style: "hide_div"});
    	const borrower_search_criteria = this.state.searchBorrower;
 		if(Object.keys(borrower_search_criteria).length > 0){
		  borrowerSearch(this.req({borrower_search_criteria}))
		        .then((response) => {
		        	
		        	this.resp(response);
	          if(!response){return };
		           	      var borrowerResults = [];
		           		if(response.data.results.length > 0){
		           			this.borrower =  response.data.results;

							this.showBorrowers();							

			           	}else{
			          		alert("No results found.");   		
			           	}
		          }
		        );
		}else{
			alert("Please enter valid criteria")
		}					        	
 }
render(){
return (
	      <div className="container borrower_left">
	      <div className="row">
	      	<h3>SEARCH - Customer </h3>
	      </div>
           	 <div className={this.state.modify_search}>
				<button type="button" className="btn btn-md modify" id="float_style"onClick={this.handleModifySearch} >Modify Search</button>
			</div>
           	<div className={this.state.search_div_style}>
          	<SearchLoanCriteria  id="searchBorrower" mode="borrower" 
									onSearch={this.handleSearch} 
									onComponentChange={this.handleComponentChange} 
									onComponentReset={this.handleComponentReset} 
									onComponentElementChange={this.handleComponentElemetChange}/> 
			</div>
            <div className={this.state.datatable_div_style}>
						<div id=''>
                        <div id='datatable' className="borrower_table">  
                        <table id ='borrower_list'  className='display strip row-border order-column list flow-data-table'>
		                  <thead>
		                  <tr>
							<th>FLOW Cust ID</th>
							<th>DP Cust ID</th>
							<th>Category</th>
							<th>Business Name</th>
							<th>Mobile Number</th>
							<th>Data Provider</th>							
							<th>Total FAs</th>
							<th>% Late Pay</th>
							<th>Open FA / APPL</th>
							<th>Remarks</th>
							<th>Status</th>
							{this.state.can_edit &&
								<th>Action</th>
							}
							<th>Action</th>
							<th>Account</th>
							<th>Action</th>
							
		                  </tr></thead>
		                  <tbody> 
		                     {this.state.borrowerResults}
		                  </tbody>
		                </table>  
		                </div> 
						<Modal show={this.state.modalBox} onHide={this.handleModalClose} size="xl">
						<Modal.Header closeButton show={this.state.modalBox} onHide={this.handleModalClose}>
							<Modal.Title>Customer Accounts : {this.state.cust_id}</Modal.Title>
						</Modal.Header>
						<Modal.Body>
							<AccountContainer dp_cust_id={this.data_prvdr_cust_id} cust_name={this.person_name} entity="customer" entity_id={this.state.cust_id}/>
						</Modal.Body>
						<Modal.Footer>
							<Button variant="secondary" onClick={this.handleModalClose}>Close</Button>
						</Modal.Footer>
						</Modal>
		              </div>    
					</div> 	     					
          
          </div>


	);
}
}
export default BorrowerSearchContainer;