import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import '../../../styles/flow.css';
import {Form, Col , Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {getLoanSearchCriteria} from '../../../actions/common_actions';
import DatePicker from 'react-datetime';
import moment from 'moment';
class SearchLoanCriteria extends FlowComponent{

constructor(props){
    super(props);
    this.state = {
      action : "Approved",	
      adv_search: false,
      show_by_today : false,
      show_from_until: false,
      today: false,
      screen: "",
      search_by_dropdown_style: "show_div",
      search_by_textbox_style: "show_div",
      register_date: null,
      __from:"",
      __to:"",
      reg_date_from:"",
      reg_date_to:"",
      section1: "show_div",
      section3: "show_div",
      value : "enabled"
    }; 
}

	handleSection1 = (value) =>{
		value = moment(value).format("YYYY-MM-DD");
		if(value.target){
			const event = value;
			value = event.target.value
		}
		if(value != "")
		{
			this.setState({section3: "disable_div",search_by_textbox_style:"disable_div"});
		}

		/*else{
			this.setState({section1:"show_div",search_by_textbox_style:"show_div"});
		}*/
	}

	handleSection3 = (event) =>{
		if(event.target.value != "")
		{
			this.setState({section1: "disable_div"});
		}
		else if(event.target.value == "")
		{
		this.setState({section1:"show_div"});	
		}
		/*else{
			this.setState({section3:"show_div"});
		}*/
	}

	handleStatusChange = (event) => {
		this.handleReset(event);
		this.handleElementReset(this.state.action_date_field + '__from');	
		this.handleElementReset(this.state.action_date_field + '__to');	
		this.handleElementReset(this.state.action_date_field + '__today');	
		this.handleElementReset(this.state.actor_id);
		this.setState({submitted_field_style: "enable_div",__from: "", __to: "", today: false,
		show_by_today : false,  show_from_until: false, show_approver: false})
		

		if(this.props.mode == 'loan' ){
			
			if(event.target.value == "pending_disbursal"){
				
				this.setState({action: "Approved", show_by_today : true, show_from_until : true, 
					actor_id: "loan_approver_id", action_date_field: "loan_approved_date",
					actor_list_dd: this.state.approver_list_dd,  
					 });
			}
			else if(event.target.value == "ongoing" || event.target.value == "disbursed"){
				this.setState({action: "Disbursed", show_from_until : true, 
					actor_id : "disbursed_by", action_date_field: "disbursal_date",
					actor_list_dd: this.state.disbursed_by_dd, 
					__from: "", __to: ""});
			}else if(event.target.value == "due" || event.target.value == "overdue"){
				
			}else if(event.target.value == "settled" ){
				this.setState({action: "Payment Captured",show_by_today : true, show_from_until : true, 
					actor_id: "paid_by", action_date_field: "paid_date",
					actor_list_dd: this.state.payer_list_dd, 
					  __from: "", __to: ""});
			}	
		}
		else if (this.props.mode == 'loan_appl') {
			if(event.target.value == "approved"){
				this.setState({action: "Approved", show_by_today : true, show_from_until : false, 
					actor_id: "loan_approver_id", action_date_field: "loan_approved_date",
					actor_list_dd: this.state.approver_list_dd,  
					 });
			}else if(event.target.value == "pending_approval"){
				this.setState({action: "Applied", show_by_today : true, show_from_until : true,  show_approver : true,
					actor_id: "loan_applied_by", action_date_field: "loan_appl_date",
					actor_list_dd: this.state.applier_list_dd,  
					 });
			}
		}
	}	

	handleDropdownValue = (event) => {
		if(event.target.id == 'status'){
			this.handleStatusChange(event)			
		}

		if(event.target.id == 'data_prvdr_code'){
			this.load_dp_rel_mgr(event.target.value);
		}
		
		if(event.target.value != ""){
			this.setState({search_by_textbox_style: "disable_div"});
		}else{
			this.setState({search_by_textbox_style: "show_div"});
		}
	}

	componentWillMount(){

	  	const country_code = this.country_code;
	  	if(this.props.mode == 'loan'){
			this.screen = "loan_search";
			this.setState({doc_id : "loan_doc_id"});
			this.setState({doc_id_lbl : "Float Advance ID"});
		}else if(this.props.mode == 'loan_appl'){
			this.screen = "loan_appl_search";
			this.setState({doc_id : "loan_appl_doc_id"});
			this.setState({doc_id_lbl : "Float Advance Application ID"});
		}
		else if(this.props.mode == 'borrower')
		{
   
			this.screen = "borrower_search";
		}
	  	this.handleApiCall(country_code,this.screen); 	

	}

handleApiCall = (country_code,screen) => {
	
	getLoanSearchCriteria(this.req({country_code, screen, "status": "enabled"}))
	  		  .then((response) => {
	  		  	 this.resp(response);
	     		 if(!response){return };
	    		var result = response.data;

				for (var dd_name in result) {
					var name_list = result[dd_name];
					//alert(JSON.stringify(name_list));
					if(dd_name == "loan_status" || dd_name == "loan_appl_status" || dd_name == "borrower_status" || dd_name == 'region_list'){	
						const data_holders = ["data_code", "data_value"];
						this.getSelectOptionsFromResult(name_list, dd_name, data_holders);
					}else{	
						this.getSelectOptionsFromResult(name_list, dd_name);
					}		
				}
		   });
}

toggleAdvSearch = () => {
	this.setState({adv_search : !this.state.adv_search});
}
SearchByFromUntil = (space) => (
		<Form.Row >
				{this.state.show_approver && 
				 <Form.Group as={Col} md={2}>
			      	<Form.Label>Approver</Form.Label>
			      	<Form.Control as="select"  id="loan_approver_id" onChange={(e) => {this.handleChange(e); this.handleDropdownValue(e)}}>
			      		{this.state.approver_list_dd}
			      	</Form.Control>
    			</Form.Group>
    		}

				{this.state.show_by_today && 
				<>
                {this.props.mode == "loan_appl" ?
			    <Form.Group as={Col} md={2}>
			      	<Form.Label>{this.state.action} By</Form.Label>
			      	<Form.Control as="select"  id={this.state.actor_id} onChange={(e) => {this.handleChange(e); this.handleDropdownValue(e)}}>
			      		{this.state.actor_list_dd}
			      	</Form.Control>
    			</Form.Group>
    			:
                 <Form.Group as={Col} md={3}>
			      	<Form.Label>{this.state.action} By</Form.Label>
			      	<Form.Control as="select"  id={this.state.actor_id} onChange={(e) => {this.handleChange(e); this.handleDropdownValue(e)}}>
			      		{this.state.actor_list_dd}
			      	</Form.Control>
    			</Form.Group>
    		    }
    		    {this.props.mode == "loan_appl" ?  
    			<Form.Group as={Col} md={2}>
			     	<Form.Label></Form.Label>
			     	<Form.Row className="justify-content-md-center">
    			 		<Form.Check className="aggregation" type="checkbox" checked={this.state.today} id={this.state.action_date_field + "__today"} onChange={(e) => {this.handleCheckboxClick(e); this.handleDropdownValue(e)}} label={this.state.action + "  Today"} />
  					</Form.Row>
    			</Form.Group>
    			:
    			<Form.Group as={Col} md={3}>
			     	<Form.Label></Form.Label>
			     	<Form.Row className="justify-content-md-center">
    			 		<Form.Check className="aggregation" type="checkbox" checked={this.state.today} id={this.state.action_date_field + "__today"} onChange={(e) => {this.handleCheckboxClick(e); this.handleDropdownValue(e)}} label={this.state.action + "  Today"} />
  					</Form.Row>
    			</Form.Group>
    		    }
    			</>
    	
    		}
    		
			{this.state.show_from_until && 
			<>
    			<Form.Group as={Col} md={space} className={this.state.section1}>
			      	<Form.Label>{this.state.action} from</Form.Label>

			      	<DatePicker closeOnSelect={true} value={this.state.__from}  dateFormat="DD MMM YYYY" timeFormat={false} required="required"
			      	onChange={(value) => {this.handleDateChange(this.state.action_date_field + '__from', value); this.setState({__from:value}); this.handleSection1(value);}} />
    			</Form.Group>
    			<Form.Group as={Col} md={space} className={this.state.submitted_field_style}>
			      	<Form.Label>{this.state.action} until</Form.Label>
			      	<DatePicker closeOnSelect={true} value={this.state.__to}  dateFormat="DD MMM YYYY" timeFormat={false} required="required"
			      	onChange={(value) => {this.handleDateChange(this.state.action_date_field + '__to', value); this.setState({__to:value}); this.handleSection1(value);}}  />

    			</Form.Group>
    	
    		</>
    		
    		}
    		
    		</Form.Row>		
)

SearchBorrower = () => (

	<Form.Row >
	           	<Form.Group as={Col}  md={3} className={this.state.section1}>
			      	<Form.Label>Flow Relationship Manager</Form.Label>
			      	<Form.Control as="select" id="flow_rel_mgr_id" onChange={(e) => {this.handleChange(e);this.handleSection1(e);}}>
			           {this.state.flow_rel_list_dd}
			      	</Form.Control>
    			</Form.Group>
			    <Form.Group as={Col}  md={3} className={this.state.section1}>
			      	<Form.Label>Region</Form.Label>
			      	<Form.Control as="select" id="region" onChange={(e) => {this.handleChange(e);this.handleDropdownValue(e);this.handleSection1(e);}}>
			      		{this.state.region_list_dd}
			      	</Form.Control>
    			</Form.Group>
				<Form.Group as={Col} md={1} className={this.state.search_by_textbox_style}>
	    			<br/>
	    			<h4>OR</h4>
	    			
    			</Form.Group>
    			<Form.Group as={Col} md={3} className={this.state.section3}>
				        <Form.Label>Any Customer ID / Mobile No / National ID</Form.Label>
				        <Form.Control id="req_parameter" onChange={(e) => { this.handleChange(e);this.handleSection3(e)}}>         
	            		</Form.Control>
    			</Form.Group> 
    			

  			  </Form.Row>		
	)

WildCardSearch = () => (
	<div className={this.state.search_by_textbox_style}>
   	
    			<Form.Group as={Col} md="2">
	    			<h4>OR</h4>
    			</Form.Group>
                <Form.Group as={Col} md="3">
			    	  <Form.Label>{this.state.doc_id_lbl}</Form.Label>
			      	  <Form.Control  id={this.state.doc_id} onChange={(e) => { this.handleChange(e); this.handleTextboxValue(e); this.handleSection3(e)}}>
			    	  </Form.Control>
    			</Form.Group>
		  	</div>
		
);

AdvancedSearch = () => (
	<>
	<Form.Row >
		<Form.Group as={Col}  md={3} className={this.state.section1}>
	      	<Form.Label>Master Customer ID</Form.Label>
	            <Form.Control id="master_cust_id" onChange={(e) => {this.handleChange(e);this.handleSection1(e)}} >         
        		</Form.Control>
		</Form.Group>
		<Form.Group as={Col}  md={3} className={this.state.section1}>
					<Form.Label>Registered from</Form.Label>

				    <DatePicker closeOnSelect={true} value={this.state.reg_date_from} dateFormat="DD MMM YYYY" timeFormat={false} required="required"
					   onChange={(value) => {this.handleDateChange("reg_date__from",value); this.setState({reg_date_from:value}); this.handleSection1(value);}}/>

		</Form.Group>
	</Form.Row>
	<Form.Row >	
		<Form.Group as={Col} className={this.state.section1}>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="not_have_active_agrmnt" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Don't have Active Agrmt" />
			</Form.Row>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="have_active_agrmnt" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Have Active Agrmt" />
			</Form.Row>
		</Form.Group>

		<Form.Group as={Col} className={this.state.section1}>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="non_active_cust" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Non Active Customers" />
			</Form.Row>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="active_cust" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Active Customers (30 Days)" />
			</Form.Row>
		</Form.Group>
			<Form.Group as={Col} className={this.state.section1}>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="have_overdue_loan" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Have Overdue FA" />
			</Form.Row>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="not_have_overdue_loan" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Don't have Overdue FA" />
			</Form.Row>
		</Form.Group>
		
		<Form.Group as={Col} className={this.state.section1}>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="have_pending_loan_appl" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Have FA Appl" />
			</Form.Row>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="not_have_pending_loan_appl" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Don't have FA Appl" />
			</Form.Row>
		</Form.Group>

		<Form.Group as={Col} className={this.state.section1}>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="have_ongoing_loan" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}}  label="Have Ongoing FA" />
			</Form.Row>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="not_have_ongoing_loan" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}}   label="Don't have Ongoing FA" />
			</Form.Row>
		</Form.Group>
	
		
		<Form.Group as={Col} className={this.state.section1}>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="do_not_have_score" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Don't have New Cust Score" /><br/>
			</Form.Row>
			<Form.Row className="justify-content-md-left">
				<Form.Check className="aggregation"  type="checkbox" id="have_score" onChange={(e) => {this.handleBorrowerCheckboxClick(e);this.handleSection1(e)}} label="Have New Cust Score" />
			</Form.Row>
		</Form.Group>

	  </Form.Row>	
	  </>
	  )


handleTextboxValue = (event) =>{
	if(event.target.value != "")
	{
		this.setState({search_by_dropdown_style: "disable_div", search_by_textbox_style : "show_div",section1: "disable_div"});
	}else{
		this.setState({search_by_dropdown_style: "show_div"});
	}
}

handleCheckboxClick = (event) => {
	if(event.target.checked)
	{
		event.target.value = true;

		this.setState({submitted_field_style: "disable_div", __from: "", __to: "", today: true});
		this.handleElementReset(this.state.action_date_field + '__from');	
		this.handleElementReset(this.state.action_date_field + '__to');	

	}else{
		event.target.value = false;
		event.target.value = null;
		this.setState({submitted_field_style: "show_div", today: false});
	}
	this.handleChange(event);
}

handleBorrowerCheckboxClick = (event) =>{
	if(event.target.checked)
	{
		event.target.value = true;
	}
	else
	{
		event.target.value = false;
	}
	this.handleChange(event);
}


handleResetClick = (event) => {
	this.handleReset();
	this.setState({search_by_dropdown_style: "show_div"});
	this.setState({search_by_textbox_style: "show_div"});
	this.setState({submitted_field_style: "show_div"});
	this.setState({approved_field_style: "show_div"});
}

handleChangeReset = ()=>{
	this.setState({__from:"",__to:"",reg_date_from:"",reg_date_to:"",section1:"show_div",section3:"show_div"});
}
render(){

	return(
		<Form onReset={this.handleChangeReset}>
			<div className={this.state.section1}>
			<Form.Row>
				<Form.Group as={Col}>
				{this.props.mode == 'borrower' ?
				<>
				<Form.Label>Status</Form.Label>
				</>
				:
				<>
			    <Form.Label>Status</Form.Label><font className="text-danger">   *  </font> 	
			    </>
			    }
			  
			      	<Form.Control as="select" id="status"   onChange={(e) => { this.handleDropdownValue(e);this.handleChange(e);this.handleSection1(e);  }} required>
			      		{ this.props.mode == 'loan_appl' && this.state.loan_appl_status_dd}
    					{ this.props.mode == 'loan' && this.state.loan_status_dd}  
                        { this.props.mode == 'borrower' && this.state.borrower_status_dd}  
			      	</Form.Control>
    			</Form.Group>
			   <Form.Group as={Col}>
			     	<Form.Label>Data Provider</Form.Label>
			        <Form.Control id="data_prvdr_code"  as="select" onChange={(e) => { this.handleChange(e); this.handleDropdownValue(e);this.handleSection1(e); }}>
                		{this.state.data_prvdr_name_list_dd}
            		</Form.Control>
    			</Form.Group> 
    			<Form.Group as={Col}>
			      	<Form.Label>DP Relationship Manager</Form.Label>
			      	<Form.Control as="select" id="dp_rel_mgr_id" onChange={(e) => {this.handleChange(e); this.handleDropdownValue(e);this.handleSection1(e);}}>
			           {this.state.data_prvdr_rel_mgr_dd}
			      	</Form.Control>
    			</Form.Group>
    			<Form.Group as={Col}>
			     	<Form.Label>Lender</Form.Label>
			      	<Form.Control as="select" id="lender_code" onChange={(e) => { this.handleChange(e); this.handleDropdownValue(e);this.handleSection1(e); }}>
			      		{this.state.lender_name_list_dd}
			     	</Form.Control>
    			</Form.Group>
    			
    		 {(this.props.mode == 'loan' || this.props.mode == 'loan_appl')&&
				<Form.Group as={Col}>
					<Form.Label>Product Name</Form.Label>
					<Form.Control id="product_id" as="select" onChange={(e) => { this.handleChange(e); this.handleDropdownValue(e); }}>			      
						{this.state.product_name_list_dd}
					</Form.Control>
				</Form.Group>
			}
           </Form.Row>
           	</div>

    	    {(this.props.mode == 'loan' || this.props.mode == 'loan_appl') && 
    	   	<>
	    	   	<div className={this.state.search_by_dropdown_style}>
	    	    	{this.SearchByFromUntil(this.props.mode == 'loan' ? 3 : 0)}
	    	    </div>
	    	      {this.WildCardSearch()}
	    	    
    	    </>
    		}	
    	    
    	    {this.props.mode == 'borrower' && 
    	    <>
    	    {this.SearchBorrower()}
    	    {this.state.adv_search  && this.AdvancedSearch()}

    	    </>
    		}
	
		  	<Form.Row className="justify-content-md-center">
				<Button type="reset" onClick={this.handleResetClick} >Reset</Button>	&nbsp;  		
				<Button className="pull-right" variant="primary" onClick={this.props.onSearch}>Search</Button>	
				{this.props.mode == 'borrower' && 
					<div style={{paddingLeft: '10px'}}>
	    			<Link to="#" onClick={this.toggleAdvSearch} 
					>Advanced Search</Link>	
					</div>
				}	
			</Form.Row>
  		</Form>

		);
    }
}
export default SearchLoanCriteria;
